export const diastolicRes = (diastolDysf, diastolDysfPlus) => {
    if (diastolDysf && diastolDysfPlus) {
         return ` :  ${diastolDysf}, ${diastolDysfPlus}. `;
    }

    if (diastolDysfPlus) {
         return ` : ${diastolDysfPlus}. `;
    }

    if (diastolDysf) {
         return ` :  ${diastolDysf}. `;
    }
    return '';
   
}