import { comaDotFix } from '../../helpers/comaDotFix';
 
 /////  
export const evaluateValvesInsufficiency = (mitral, aortic, tricuspid, laValve) => {
    let mitralText = '';
    let aorticText = '';
    let tricuspidText = '';
    let laValveText = '';

    ////     
    switch (true) {
        case mitral === "" && aortic === "" && tricuspid === "" && laValve === "":
            return '';
            break;
        
        case mitral !== "" || aortic !== "" || tricuspid !== "" || laValve !== "":
           
            ////           
             if (mitral !== "") {
                mitralText = ` : ` + mitral + ', '; 
            }
            
            if (aortic !== "") {
                aorticText = ` : ` + aortic + ', ';
            }

            if (tricuspid !== "") {
                tricuspidText = ` : ` + tricuspid + ', ';;
            }

            if (laValve !== "") {
                laValveText = `  : ` + laValve + ', ';
            }
          ////////

            return comaDotFix(` ${mitralText}${aorticText}${tricuspidText}${laValveText}. `);
            break;
        
        default:
            return 'Normal';
            break;
    }

}