export const valvesAddInfo = (mAdd, aAdd, tAdd, laAdd) => {
    let r = '';

     if (mAdd || aAdd || tAdd || laAdd) {
        
           r = ` . `;
    }
    
    
    if (mAdd) {
        r += `: ${mAdd}. `
    }
    if (aAdd) {
        r += `: ${aAdd}. `
    }
    if (tAdd) {
        r += `: ${tAdd}. `
    }
    if (laAdd) {
        r += `: ${laAdd}. `
    }
 
    return r;
}