import { refs } from "../refs";
export const copyResult = (formData, result, segmTextCopy) => {
  
    if (result === '') {
        return '';
    }

    const { rv, la, aorta, ivs, lvWall, lv, ef } = formData;

    return `  ${rv} .   ${la} .  ${aorta} .   ${ivs} .    ${lvWall} .  ,  ${lv} .  ${ef}%. : ${result}  ${segmTextCopy}`;
 }